<template>
<el-row :gutter="5" id ='audio1' type='flex'>
  <audio ref='audio' :src='audioUrl'></audio>
  <el-col :span="2"><el-button icon="el-icon-caret-right" size="medium" circle @click="playMusic"></el-button></el-col>
  <el-col :span="2"><el-button icon="el-icon-d-caret" size="medium" circle @click="stopMusic"></el-button></el-col>
  <el-col :span="20"><el-slider v-model="slider" :show-tooltip="false" @change="changeSlider" ></el-slider></el-col>
</el-row>
</template>

<script>
export default {
  name: 'audio1',
  data(){
    return{
      audioUrl:require("../static/123.mp3"),
      musicTime:0,
      slider:0,
      timer:null
    }
  },
  methods:{
    playMusic:function(){
      this.$refs.audio.play();
      this.timer = setInterval(this.countTime,1000);
    },
    stopMusic:function(){
      this.$refs.audio.pause();
      clearInterval(this.timer)
      this.slider = 0;
      this.musicTime =0;
    },
    countTime:function(){
      this.musicTime+=1;
      this.slider = (this.musicTime*100/this.$refs.audio.duration);
    },
    changeSlider:function(){
      this.$refs.audio.currentTime = (this.slider*this.$refs.audio.duration)/100;
      this.musicTime = this.$refs.audio.currentTime;
    }
  },
}
</script>
